#home{

    position: relative;
    background: url('../../constants/assets/bgIMG.png');
    background-size: cover;
    background-repeat: repeat;
    background-position: center;
    

    .app__wrapper{
        padding: 0;
        
        .copyright{
            display: none;
        }

    }


}
.app__header{
    flex:1 ;
    width: 100%;
    height: 100%;
    flex-direction: row;

    padding: 6rem 2rem 0;

    @media screen and (min-width: 2000px){
        padding-top: 8rem;;
        
    }
    @media screen and (max-width: 1200px){
        padding-top: 8rem;
        flex-direction: column;;
        
    }
    @media screen and (max-width: 450px){
        padding-top: 6rem 1rem 2rem;
        
    } 

}
.app__header-info{
    flex: 0.65;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    
    height: 100%;

    @media screen and (max-width:2000){
        width: 100% ;   
        margin-right: 0rem;;     
    }

}
.app__header-badge{
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;

    flex-direction: column;

    .badge-cmp, .tag-cmp{
        padding: 1rem 2rem;
        border: var(--white-color);
        border-radius: 15px;
        flex-direction: row;
        width: auto;

        box-shadow: 0 0 20px rgba(0,0,0,0.2);
    }
    .tag-cmp{
        flex-direction: column;
        margin-top: 3rem;
        p{
            width: 100%;
            text-transform: uppercase;
            text-align: left;
        }
        span{
            font-size: 2.2rem;
            // font-weight: bold;

            @media screen and (max-width: 2000px){
                font-size: 5rem;
            }
            @media screen and (max-width:1200px){
                justify-content: flex-start;
                align-items: flex-start;
            }
        } 
    }
}

.app__header-circle {
    flex: 0.75;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
    height: 100%;
    margin-left: 1rem;

    div{
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: var(--white-color );
        box-shadow: 0 0 20px rgba(0,0,0,0.2);
        margin: 2px;
    }
    img{
        width: 60%;
        height: 60%;
        // border-radius: 50%; 
    }
    div:nth-child(odd){
        width: 100px;
        height: 100px;
    }
    div:nth-child(even){
        width: 150px;
        height: 150px;
        margin: 1.75rem;
    }
    @media screen and (min-width: 1200px){
        div:nth-child(1){
            width: 400px;
            height: 400px;
        }
        div:nth-child(even){
            width: 150px;
            height: 150px;
            margin: 1.75rem;
        }
        div:nth-child(1){
            width: 200px;
            height: 200px;
        }
    }
    @media screen and (max-width: 1200px){
        width: 100%;
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 0;
    
        div{
            margin: 1rem;
        }

    }

}
.app__header-img{
    float: 1;
    height: 100%;

    display: flex;
    align-items: flex-end;
    justify-content: flex-end;

    position: relative;

    img{
        width: 100%;
        z-index: 1;
        object-fit: contain;
        
        
    }
    .overlay_circle{
        position: absolute; 
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 0;
        width: 100%;
        height: 90%;
    }
    @media screen and (max-width:1200px){
        margin: 2rem 0;
    }
}