.app__footer{
    flex: 1;
    flex-direction: column;
    width: 100%;
}
.app__footer-cards{
    width: 60%;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap-reverse;

    margin: 4rem 2rem 2rem;


    .app__footer-card{
        min-width: 290px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: flex-start;

        margin: 1rem 0;
        padding: 1rem;
        cursor: pointer;

        padding: 1rem;
        border-radius: 10px;

        background-color: var(--primary-color);
        transition: all 0.3s ease-in-out;

        img{
            width: 40px;
            height: 40px;

            margin: 0 0.7rem;
        }
        p{

            font-weight: 600;
        }
        a{
            text-decoration: none;
            font-weight: 600;
        }
        &:hover{
            box-shadow: 0 0 25px var(--primary-color);
        }

        @media screen and (max-width: 450px){
            width: 100%;
        }

    }
    @media screen and (max-width: 760px){
        width: 100%;
    }

}

.app__footer-cards .app__footer-card:last-child{
    background-color: var(--primary-color);
}

.app__footer-form{
    width: 60%;
    flex-direction: column;
    margin: 1rem 2rem;

    div{
        width: 100%;margin: 0.75rem 0;
        border-radius: 10px;
        cursor: pointer;

        background-color: var(--primary-color);
        transition: all 0.5s ease-in-out;

        input,textarea{
            width: 100%;
            padding: 0.95rem;

            border: none;
            border-radius: 7px;
            background-color: (--primary-color);

            border-color: var(--primary-color);
            font-family: var(--font-base);
            outline: none;

        }

        textarea{
            width: 100%;
            height:  180px;
        }

        &:hover{
            box-shadow: 0 0 25px  var(--primary-color);
        }

    }

    button{
        padding: 1rem 2rem;
        border-radius: 10px;
        border: none;
        background-color: var(--secondary-color);

        font-weight: 500;

        color: var(--white-color);
        outline: none;

        margin: 2rem 0rem 0rem 0rem;
        font-family: var(--font-base);
        cursor: pointer;
    }


    @media screen and(max-width:768px) {
        width: 100%;
        margin: 1rem 0;
    }
}